.App {
  display: flex;
  font-family: "Poppins", sans-serif;
}

.main {
  display: flex;
  gap: 71px;
}

* {
  box-sizing: border-box;
  outline: none;
}

body {
  margin: 0;
}

h1,
h2,
h3,
h4,
h5,
h6,
p,
ul,
li {
  margin: 0;
  padding: 0;
}

ul {
  list-style-type: none;
}

a {
  text-decoration: none;
}